<template>
  <div>
    <van-tabbar v-model="current" @change="toggleTab" active-color="steelblue" inactive-color="lightsteelblue">
      <van-tabbar-item :icon="item.icon" v-for="(item , index) in tabData" :key="index">{{item.title}}</van-tabbar-item>
    </van-tabbar>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
    data(){
        return{
            current:0,
            tabData:[
              {
                title:"书架",
                icon:"smile-o",
                name:"Bookrack",
                url:"/main/bookrack"
              },
              {
                title:"书城",
                icon:"shop-o",
                name:"Bookstore",
                url:"/main/bookstore"
              },
              {
                title:"分类",
                icon:"apps-o",
                name:"Classify",
                url:"/main/classfiy"
              },
              {
                title:"我的",
                icon:"user-o",
                name:"My",
                url:"/main/my"
              },
            ]
        }
    },
    created(){
      let hash = location.hash.slice(1);
      for(let i = 0 ; i < this.tabData.length ; i++){
        if(this.tabData[i].url == hash){
          this.current = i;
          break;
        }
      }
    },
    methods:{
      toggleTab(index){
        this.$router.push({name:this.tabData[index].name})
      }
    }
};
</script>
